<script>
  function alerthello () {
    alert('hello')
  }

  let called = 0
  import Vue from 'vue'
  import Child from './Child'
  import Sibling from './Sibling'
  import ChildSlot from './ChildSlot'
  export default {
    data(){
      return {
        isFirst: true,
        hide: false,
        keepChild: false
      }
    },
    computed: {
      getText(){
        return this.hide ? 'show' : 'hide'
      }
    },
    methods: {
      console(){
        console.log(this.$children);
      },
      hideBtn (elm) {
        elm.classList.add('btn-red');
        elm.addEventListener('click', alerthello, false);
      }
    },
    beforeCreate(){
      console.log('%c * parent:beforecreate', 'font-size: 21px;color:blue');
    },
    created(){
      window.vueconsole = this.console.bind(this);
    },
    components: {
      'child': Child,
      'sibling': Sibling,
      'child-slot': ChildSlot
    },
    render(h) {
      var vm = this;
      var _child = this.hide ? null : h('child', { attrs: { tag: 'section' } });
      if ( vm.keepChild ) {
        _child = h('keep-alive', [ _child ])
      }
      return h('div', { class: [ 'parent' ] },
        [ h('h1', 'parent called: ' + ++called),
          h('button', {
            class: 'btn-green', attrs: { type: 'button' },
            on: {
              click: function () {
                vm.keepChild = !vm.keepChild;
              }
            }
          }, 'keep alive is ' + vm.keepChild),
          h('button', {
            class: 'btn-green', attrs: { type: 'button' },
            on: {
              click: function () {
                vm.hide = !vm.hide;
              }
            }
          }, 'click to ' + vm.getText),
          h('button', {
            class: 'btn-green self-hide', attrs: { type: 'button' },
            on: {
              click: function () {
                vm.hideBtn(document.querySelector('.child').querySelector('button'));
              }
            }
          }, 'attach event'),
          _child,
          h('sibling'),
          h('child-slot', [ h('p', 'I\'m from parent') ])
        ]
      )
    }
  }
</script>


