<script>
  let called=0
  export default {
    props: {
      num: {
      }
    },
    beforecreate(){
    console.log('grandChild:beforecreate');
  },
  created(){

    console.log('grandChild:created');
  },
  beforeMount(){

    console.log('grandChild:beforemount');
  },
  mounted(){
    console.log('grandChild:mounted');
  },
    render(h){
      return h('div',{class:['grand-child']},[/*`It's from prop : ${this.num}`,*/ h('h1',`grand-child called: ${++called}`)]);
    }
  }
</script>



