<script>
  let called = 0
  export default {
    props:{
      tag: {default:'div'}
    },
    render(h){
      return h(this.tag,{class:['child-slot']},[
        this.$slots.default,
        h('h1','childslot called:'+ ++called)])
    }
  }
</script>


