
<script>

  let called=0;
  import GrandChild from './GrandChild';
  export default {
    props: {
      tag: {
        default: 'div'
      }
    },
    beforeCreate(){
      console.log('%c 2. child:beforecreate', 'font-size: 20px;color:#f00');
    },
    data(){
      return {
        clickedNumber: called,
        msgp:'paragraph',
        rendered: 0,
        isBlue: true
      }
    },
    created(){
      window.childComp = window.childComp || [];
      window.childComp.push(this)
      console.log('%c 2. child:created', 'font-size: 20px;color:#f00');
    },
    beforeMount(){
      console.log('%c 2. child:beforemount', 'font-size: 20px;color:#f00');
    },
    mounted(){
      console.log('%c 2. child:mounted', 'font-size: 20px;color:#f00');
    },
    beforeDestroy (){
      console.log('%c 2. child:beforedestroy', 'font-size: 20px;color:#f00');
    },
    destroyed(){
      console.log('%c 2. child:destroyed', 'font-size: 20px;color:#f00');
    },
    deactivated(){
      window.oldChildComp=this;
      console.log('%c 2. child:deactivated', 'font-size: 20px;color:#f00');
    },
    activated(){
      window.newChildComp=this;
      console.log('%c 2. child:activated', 'font-size: 20px;color:#f00');
    },
    render(h){
      ++called
      console.log('%c 2. child:render', 'font-size: 20px;color:#f00');
      var vm = this;
      return h(this.tag, {class:['child'], is:this.tag},
       [
        h('h1','child called: '+ called),
        h('button',{
          class: vm.isBlue? 'btn-blue': 'btn-white',
          on:{'dblclick': function(){vm.isBlue=!vm.isBlue}},
          keepAlive: true
        },'click'),
        h('grand-child',{
//          props:{num:vm.clickedNumber},
          nativeOn: {click: function(){alert('called nativeOn(event attaching to component)')}}
          //on: {click: function(){console.log('on',this)}}
          }
        )])
    },
    components: {
      'grand-child':GrandChild
    },
    beforeUpdate(){
      console.log('%c 2. child: beforeupdate', 'font-size: 20px;color:#f00');
    },
    updated(){
      console.log('%c 2. child: updated', 'font-size: 20px;color:#f00');
    }
  }
</script>


